import React from "react";
import { Button } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import styles from "./popup.module.scss";
import { handleModal } from "../../services/modalSlice";

function Popup() {
  const dispatch = useDispatch();
  // eslint-disable-next-line react-redux/useSelector-prefer-selectors
  const isOpen = useSelector((state) => state.modal.isOpen);
  const navigate = useNavigate();

  const handleClose = () => {
    dispatch(handleModal(false));
  };

  React.useEffect(() => {
    function onEsc(evt) {
      if (evt.code === "Escape") {
        handleClose();
      }
    }
    document.addEventListener("keydown", onEsc);
    return () => document.removeEventListener("keydown", onEsc);
  });

  return (
    <div
      className={`${styles.container} ${isOpen ? styles.container_opened : ""}`}
    >
      <div className={styles.popup}>
        <div className={styles.block}>
          <h2 className={styles.h2}>  </h2>
          <ul className={styles.unsortedList}>
            <li className={styles.listItem}>
              <h3 className={styles.h3}></h3>
              <p className={styles.paragraph}> UI </p>
            </li>
            <li className={styles.listItem}>
              <h3 className={styles.h3}></h3>
              <p className={styles.paragraph}></p>
            </li>
            <li className={styles.listItem}>
              <h3 className={styles.h3}></h3>
              <p className={styles.paragraph}></p>
            </li>
            <li className={styles.listItem}>
              <h3 className={styles.h3}> gross</h3>
              <p className={styles.paragraph}> 60000  90000</p>
            </li>
            <li className={styles.listItem}>
              <h3 className={styles.h3}></h3>
              <p className={styles.paragraph}>
                Nunc vulputate libero et velit interdum, ac aliquet odio mattis.
                Class aptent taciti sociosqu ad litora torquent per conubia
                nostra, per inceptos himenaeos. Curabitur tempus urna at turpis
                condimentum lobortis. Ut commodo efficitur neque.
              </p>
            </li>
            <li className={styles.listItem}>
              <h3 className={styles.h3}></h3>
              <p className={styles.paragraph}>
                Nunc vulputate libero et velit interdum, ac aliquet odio mattis.
                Class aptent taciti sociosqu ad litora torquent per conubia
                nostra, per inceptos himenaeos. Curabitur tempus urna at turpis
                condimentum lobortis. Ut commodo efficitur neque.
              </p>
            </li>
            <li className={styles.listItem}>
              <h3 className={styles.h3}></h3>
              <p className={styles.paragraph}>
                Nunc vulputate libero et velit interdum, ac aliquet odio mattis.
                Class aptent taciti sociosqu ad litora torquent per conubia
                nostra, per inceptos himenaeos. Curabitur tempus urna at turpis
                condimentum lobortis. Ut commodo efficitur neque.
              </p>
            </li>
          </ul>
        </div>
        <div className={styles.block}>
          <h2 className={styles.h2}> </h2>
          <ul className={styles.unsortedList}>
            <li className={styles.listItem}>
              <h3 className={styles.h3}> HR</h3>
              <p className={styles.paragraph}>100%</p>
            </li>
            <li className={styles.listItem}>
              <h3 className={styles.h3}> HR</h3>
              <p className={styles.paragraph}>90000%</p>
            </li>
          </ul>
        </div>
        <div className={styles.block}>
          <h2 className={styles.h2}>  </h2>
          <ul className={styles.unsortedList}>
            <li className={styles.listItem}>
              <h3 className={styles.h3}> </h3>
              <p className={styles.paragraph}>1</p>
            </li>
            <li className={styles.listItem}>
              <h3 className={styles.h3}> </h3>
              <p className={styles.paragraph}>1%</p>
            </li>
            <li className={styles.listItem}>
              <h3 className={styles.h3}>    ?</h3>
              <p className={styles.paragraph}>%</p>
            </li>
            <li className={styles.listItem}>
              <h3 className={styles.h3}>  </h3>
              <p className={styles.paragraph}>
                Worem ipsum dolor sit amet, consectetur adipiscing elit. Nunc
                vulputate libero et velit interdum, ac aliquet odio mattis.
                Class aptent taciti sociosqu ad litora torquent per conubia
                nostra.%
              </p>
            </li>
            <li className={styles.listItem}>
              <h3 className={styles.h3}> </h3>
              <p className={styles.paragraph}> %</p>
            </li>
            <li className={styles.listItem}>
              <h3 className={styles.h3}> </h3>
              <p className={styles.paragraph}>%</p>
            </li>
            <li className={styles.listItem}>
              <h3 className={styles.h3}>   </h3>
              <p className={styles.paragraph}>%</p>
            </li>
          </ul>
        </div>
        <div className={styles.wrapperForButtons}>
          <div className={styles.containerForBtn}>
            <Button
              variant="contained"
              color="rqwhite"
              onClick={() => handleClose()}
              sx={{
                height: "46px",
                width: "180px",
                borderRadius: "8px",
                textTransform: "none",
              }}
            >
              <p className={styles.button_text}></p>
            </Button>
          </div>

          <div className={styles.containerForBtn}>
            <Button
              variant="contained"
              color="rqback"
              onClick={() => {
                handleClose();
                navigate("/success");
              }}
              sx={{
                height: "46px",
                width: "180px",
                borderRadius: "8px",
                textTransform: "none",
              }}
            >
              <p className={styles.button_text}> </p>
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Popup;
